*,*::before,*::after{
  box-sizing: border-box;
  margin:0;
  padding:0;
}


.container{
  
  margin:3rem auto;
  padding:2rem;
  text-align:center;
  width:50%;
  display:flex;
  flex-direction: column;
  
  align-items:center;
  
  
}
.line{
  width:5rem ;
  height:0.5rem;
  background-color:#66a3ff;
}
.user{
  display:flex;
  align-items:center;
  margin-top:2rem;
  
  
}
.user img{
  width:75px;
  height:75px;
  object-fit: cover;
  border-radius:50%;
}
.user-text{
  margin-left:1rem;
}
.btn{
  padding:0.5rem 1rem;
  color:#fff;
  background-color:#003366;
  font-size:1rem;
  border:none;
  border-radius:5px;
  cursor:pointer;
  width:30%;
  margin-top:2rem;
  margin-bottom:2rem;
  

}
.tours{
  width:50%;
  display:flex;
  flex-direction: column;
  align-items: center;
  margin:auto;
  justify-content: center;
  text-align: center;
}
.card{
  width:100%;
  background-color:#eee;
  margin-bottom:2rem;
}
.card img{
  width:100%;
  height:350px;
  object-fit: cover;

  
}
.card-txt{
  padding:1rem;
  
}
.card-txt div{
  width:100%;
  display:inline-flex;
  justify-content: space-between;
}
.card-txt p{
  line-height:1.2rem;
  
}
.load{
  position:absolute;
  
  left:50%;
  bottom:50%;
  transform: translateX(-50%)
}
body{
  background-color: hsl(210, 36%, 96%);
}
.review{
  background-color:#fff;
  width:30rem;
  height:25rem;
  position: relative;
  top:10rem;
  left:50%;
  transform: translateX(-50%)

}
.person-img {
  width: 100%;
  display: block;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  position: relative;
}
.img-container {
  position: relative;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin: 0 auto;
  top:1rem;

}
.review-txt{
  margin-top:1rem;
  text-align:center;

}
.review-txt h4{
  font-size:2rem;
}
.review-txt h2{
  color: hsl(205, 78%, 60%);
  padding:1rem;
}
.review-txt p{
  padding:0 1rem;
}
.btn-container{
  text-align: center;
  margin-top:1rem;
}
.btn-container button{
  margin:0 1rem;
  text-align:center;
  background-color: transparent;
  color:hsl(205, 78%, 60%);
  border:none;

}
.review .btn{
  position: relative;
  left:50%;
  transform: translateX(-50%)
  
}
.flex-container{
  display:inline-flex;
  justify-content: space-between;

}
.item{
  border-bottom:1px solid #000;
  padding:1rem; 

}